.input {
  width: 372px;
  height: 36px;
  border: 1px solid #D1D1D1;
  border-radius: 5px;
  padding-left: 10px;
}

.input:focus {
  outline: none;
  border: 1px solid #0066CC;
}


.inputWrapper {
}

.superInput {
}

.errorInput {
  border: 1px solid #CC1439;
  border-radius: 5px;
}

.errorInput:focus {
  border: 1px solid red;
}

.error {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  color: #CC1439;
  text-align: left;
  margin-bottom: 5px;
}
